import React from "react";
import { useSelector } from "react-redux";
import { localidadesList } from "../../../../constants/db_provincias";
import { useFormCreateUser } from "../../../../hooks/use_form_crearUsuario";
import "../crear_usuarios/crear_usuarios.css";

function CrearUsuarios() {
  const {
    auth: { fetching },
  } = useSelector((state) => state);

  const [
    formValues,
    handleInputChange,
    handlerBlur,
    onClickChangeHandler,
    handlerBlurEmail,
  ] = useFormCreateUser({
    rol: "coorporativo",
    nombre: "",
    email: "",
    codigoPostal: "",
    dni: "",
    telefPrincipal: "",
    direccion: "",
    telef1: "",
    provincia: "Loja",
    telef2: "",
    canton: "Loja",
    parroquia: "",
    password: "",
    repassword: "",
    errorMsg: "",
    errorEmail: "",
  });

  const {
    errorMsg,
    repassword,
    password,
    rol,
    nombre,
    email,
    codigoPostal,
    ruc,
    referencia,
    telefPrincipal,
    direccion,
    telef1,
    provincia,
    canton,
    parroquia,
    errorEmail,
    razonSocial,
  } = formValues;

  return (
    <div className="crearGuias col-md-9 ms-sm-auto col-lg-10 px-md-4 pt-4 scroll-section">
      <div className="container-fluid pt-4">
        <h4 className="title-separtor">Crear Usuario</h4>
        <hr />
        <div className="row align-items-center">
          <div className="col-sm-12 col-md-4 align-self-start">
            <h4 className="title-separtor pt-2">Datos de Usuario</h4>
          </div>
          <div className="col-sm-12 col-md-2 align-self-start">
            <h4 className="title-separtor pt-2">Tipo de Usuario: </h4>
          </div>
          <div className="col-sm-12 col-md-6">
            <select
              className="col  align-self-start form-select"
              placeholder="Tipo Usuario"
              name="rol"
              value={rol}
              onChange={handleInputChange}
            >
              <option value="admin">Administrador</option>
              <option value="puntoVenta">Puntos de Venta</option>
              <option value="sucursal">Sucursales</option>
              <option value="coorporativo">Clientes Coorporativos</option>
            </select>
          </div>
        </div>
        <hr />
        <div className="card card-body">
          <h5 className="mt-3 titleSection-form">Datos de Usuario</h5>
          <hr />
          <form onSubmit={onClickChangeHandler}>
            <div className="row ">
              <div className="col-lg-6 ">
                <div className="mb-3">
                  <label htmlFor="simpleinput" className="form-label-input">
                    Razón Social
                </label>
                  <input
                    type="text"
                    className="form-control"
                    placeholder="Nombres"
                    name="razonSocial"
                    value={razonSocial}
                    onChange={handleInputChange}
                  />
                </div>
                <div className="mb-3">
                  <label htmlFor="simpleinput" className="form-label-input">
                    Ruc/Cédula
                </label>
                  <input
                    type="text"
                    className="form-control"
                    placeholder="110XXXXXXXXX"
                    name="ruc"
                    value={ruc}
                    onChange={handleInputChange}
                  />
                </div>
                <div className="mb-3">
                  <label htmlFor="simpleinput" className="form-label-input">
                    Dirección
                </label>
                  <input
                    type="text"
                    className="form-control"
                    placeholder="Praza Ramón, 31, 8º F"
                    name="direccion"
                    value={direccion}
                    onChange={handleInputChange}
                  />
                </div>
                <div className="mb-3">
                  <label htmlFor="simpleinput" className="form-label-input">
                    Referencia
                </label>
                  <input
                    type="text"
                    className="form-control"
                    placeholder="Praza Ramón, 31, 8º F"
                    name="referencia"
                    value={referencia}
                    onChange={handleInputChange}
                  />
                </div>
                <div className="mb-3">
                  <label className="form-label-input">Provincia *</label>
                  <select
                    className="form-select"
                    onChange={handleInputChange}
                    value={provincia}
                    name="provincia"
                    required
                  >
                    <option key={`#1_destinatario`} value="">
                      Seleccionar la Provincia
                  </option>
                    {localidadesList.map((e) => (
                      <option
                        key={`${e.provincia}_destinatario`}
                        value={e.provincia}
                      >
                        {e.provincia}
                      </option>
                    ))}
                  </select>
                </div>
                <div className="mb-3">
                  <label className="form-label-input">Cantón *</label>
                  <select
                    className="form-select"
                    name="canton"
                    onChange={handleInputChange}
                    value={canton}
                    required
                  >
                    <option key={`#2_destinatario`} value="">
                      Seleccionar el Cantón
                  </option>

                    {localidadesList
                      .filter((item) => {
                        if (item.provincia === provincia) {
                          return item;
                        }
                      })[0]
                      ?.cantones.map((e) => {
                        return (
                          <option
                            key={`${e.canton}_canton_destinatario`}
                            value={e.canton}
                          >
                            {e.canton}
                          </option>
                        );
                      })}
                  </select>
                </div>
                <div className="mb-3">
                  <label className="form-label-input">Parroquia *</label>
                  <select
                    className="form-select"
                    name="parroquia"
                    onChange={handleInputChange}
                    value={parroquia}
                    required
                  >
                    <option key={`#2_destinatario`} value="">
                      Seleccionar la Parroquia
                  </option>

                    {localidadesList
                      .filter((item) => {
                        if (item.provincia === provincia) {
                          return item;
                        }
                      })[0]
                      ?.cantones.filter((item) => {
                        if (item.canton === canton) {
                          return item;
                        }
                      })[0]
                      ?.parroquias.map((e) => {
                        return (
                          <option key={`${e}_parroquia_destinatario`} value={e}>
                            {e}
                          </option>
                        );
                      })}
                  </select>
                </div>
              </div>

              <div className="col-lg-6 ">
                <div className="mb-3">
                  <label htmlFor="simpleinput" className="form-label-input">
                    Responsable
                </label>
                  <input
                    type="text"
                    className="form-control"
                    placeholder="Nombres Completo"
                    name="nombre"
                    value={nombre}
                    onChange={handleInputChange}
                  />
                </div>

                <div className="mb-3">
                  <label htmlFor="simpleinput" className="form-label-input">
                    Télefono Empresa
                </label>
                  <input
                    type="text"
                    className="form-control"
                    placeholder="Teléfono Empresa"
                    name="telefPrincipal"
                    value={telefPrincipal}
                    onChange={handleInputChange}
                  />
                </div>
                <div className="mb-3">
                  <label htmlFor="simpleinput" className="form-label-input">
                    Télefono Responsable
                </label>
                  <input
                    type="text"
                    className="form-control"
                    placeholder="Teléfono Responsable"
                    value={telef1}
                    name="telef1"
                    onChange={handleInputChange}
                  />
                </div>
                <div className="mb-3">
                  <label htmlFor="simpleinput" className="form-label-input">
                    Código Postal
                </label>
                  <input
                    type="text"
                    className="form-control"
                    placeholder="Código Postal..."
                    name="codigoPostal"
                    value={codigoPostal}
                    onChange={handleInputChange}
                  />
                </div>
                <div className="mb-3">
                  <label htmlFor="simpleinput" className="form-label-input">
                    Usuario
                </label>
                  <input
                    type="email"
                    className="form-control"
                    id="inputEmail"
                    placeholder="example@example.com"
                    name="email"
                    value={email}
                    onChange={handleInputChange}
                    onBlurCapture={handlerBlurEmail}
                  />
                </div>
                <div className="mb-3">
                  <label htmlFor="simpleinput" className="form-label-input">
                    Contraseña
                </label>
                  <input
                    type="password"
                    className="form-control"
                    placeholder="Contrseña del usuario"
                    name="password"
                    value={password}
                    onChange={handleInputChange}
                  />
                </div>
                <div className="mb-3">
                  <label htmlFor="simpleinput" className="form-label-input">
                    Confirmación de Contraseña
                </label>
                  <input
                    type="password"
                    className="form-control"
                    placeholder="Repite la Contrseña"
                    name="repassword"
                    value={repassword}
                    onChange={handleInputChange}
                    onBlur={handlerBlur}
                  />
                </div>
              </div>
            </div>
            <div className="card card-body">
              <div className="row">
                <button type="button" className=" btn-dt-del col mx-1 p-2 bt-a">
                  Cancelar
            </button>
                <button
                  type="submit"
                  className=" btn-dt-details col mx-1 bt-a"
                >
                  {fetching ? "Creando usuario..." : "Crear Usuario"}
                </button>
              </div>
            </div>
          </form>
        </div>


      </div>
    </div>
  );
}

export default CrearUsuarios;
